.skillContainer {
    font-size: 18px;
    line-height: 25px;
    width: 220px;
    padding: 15px 30px;
    margin-bottom: 40px;

    .skillTitleSection {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;

        .skillLogoContainer {
            transform: rotate(45deg);
            height: 80px;
            width: 80px;
            background-color: #f5df4e;
            display: flex;
            justify-content: center;
            align-items: center;
            margin-bottom: 5px;

            .skillLogo {
                height: 60px;
                width: 60px;
                color: #f5df4e;
                transform: rotate(-45deg);
                @media screen and (max-width: 415px) {
                    height: 40px;
                    width: 40px;
                }
            }

            @media screen and (max-width: 415px) {
                height: 60px;
                width: 60px;
            }
        }
    }

    @media screen and (max-width: 1023px) {
        margin-bottom: 15px;
    }
}

